@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to top right, lightseagreen, lavender);
  margin-bottom: 45px;
  font-family: 'Baloo Paaji 2', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  padding-top: 8px;
  padding-bottom: 8px;
  margin-bottom: 40px;
}
.name {
  color: #495057;
  float: left;
  padding-left: 10px;
  font-family: 'Pacifico', cursive;
  /* font-family: 'Press Start 2P', cursive; */

}

.nav-link {
  color: #495057;
  padding-top: 15px;
}

.nav-link:hover {
  color: lightseagreen;
  font-size: 18px;
}

.footer {
  background-color: #495057;
  padding: 15px;
  color: lavender;
 
}

li {
  list-style: none;
}

.copy {
  padding-top: 11px;
}

.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  padding: 4px;
}

.social-icons a{
  background-color:lavender;
  color:lightseagreen;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:40px;
  height:40px;
  border-radius: 100%;
  padding-top: 4px;
  text-align: center;
  margin-left: 4px;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color: gray;
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.github:hover
{
  background-color:#495057;
}

.social-icons a.linkedin:hover
{
  background-color:#495057;
}

.footer-icons {
  margin-left: 16%;
  height: 45px;
}


.profile {
    width: 200px;
    height: 250px;
    border-radius: 100%;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-top: 10px;
    border: 1px solid #495057;
  }

  .latest {
    width: 65%;
    height: 75%;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  
 .card-body {
  display: block;
  text-align: center;
 }

  .card-container {
    margin: 0 auto;
    position: relative;
    width: 700px;
    height: 750px;
    margin-bottom: 200px;
  }

  .card {
    /* max-width: 540px; */
    /* position: absolute; */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  button {
    border: 1px solid #495057;
    border-radius: 5%;
    background-color: #495057;
    color: lightseagreen;
    margin-right: auto;
    margin-left: auto;
    display: block;
    width: 30%;
    text-align: center;
  }

  .card-title-front {
    margin-top: 45px;
  }

  .front {
    position: absolute;
    width: 100%;
    height:100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: lavender;
    color: #495057;
    overflow: hidden;
  }

  .back {
    position: absolute;
    overflow: hidden;
    /* z-index: 2; */
    width: 100%;
    height:100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: lavender;
    color: #495057;
  }


.card {
    /* max-width: 25%; */
    
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
  }

  #proj-links {
      width: 45%;
      border-radius: 5px;
      text-decoration: none;
      border: 1px solid #777777;
      padding: 5px 10px;
      color: lightseagreen;
      background-color: #495057;
      font-weight: 900;
  }

  #contForCards {
    margin: 0 auto;
    position: relative;
    width: 300px;
    height: 350px;
    margin-bottom: 120px;
  }
  
